.content {
	float: left;
	max-width: 70.52%;
	height: 100%;
	margin-right: 15px; }
.doctor {
	margin-top: 42px;
	&__header {
		font-size: 30px;
		font-weight: 300; }
	&__about {
		margin-top: 21px; }
	&__photo {
		float: left;
		margin-right: 22px; }
	&__name {
		font-size: 18px;
		font-weight: 700;
		line-height: 24px; }
	&__exp {
		color: #666666;
		font-size: 14px;
		font-weight: 500;
		line-height: 24px;
		margin-left: 9px; }
	&__text {
		overflow: hidden;
		text-overflow: clip;
		height: 130px;
		font-size: 14px;
		line-height: 1.15em; }
	.education {
		max-width: 321px;
		&__title {
			margin-top: 20px;
			font-weight: 700;
			line-height: 24px; }
		&__item {
			font-size: 14px;
			margin-top: 20px;
			margin-left: 20px;
			&:before {
				content: "";
				display: inline-block;
				width: 8px;
				height: 8px;
				margin-right: 13px;
				margin-left: -20px;
				@include s(circle); } } }
	&__centers {
		font-weight: 700;
		line-height: 24px;
		margin-top: 40px;
		margin-bottom: 10px; } }
.proflist {

	&__item {
		display: inline-block;
		margin: 8px 10px 10px 0; }
	&__link {
		display: block;
		padding: 3px 10px;
		border-radius: 5px;
		background-color: #c4ebf6;
		color: #333333;
		font-size: 14px; } }

.centers {
	width: 100%;
	margin-bottom: 20px;
	border-radius: 5px;
	background-color: #e6f3f7;
	font-family: "Helvetica Neue", sans-serif;
	font-size: 14px;
	line-height: 22px;
	@include clr;
	padding: 15px;
	&__left {
		float: left;
		margin-left: 7px; }
	&__right {
		float: right;
		margin-right: 15px;
		@include clr; }
	&__name {
		color: #2596ca;
		font-size: 21px;
		font-weight: 300;
		line-height: 35px; }
	&__metro:before {
		content: "";
		display: inline-block;
		@include s(metro);
		margin-right: 10px; }
	&__street:before {
		content: "";
		display: inline-block;
		@include s(map);
		margin-right: 12px; }

	&__text {
		margin-top: 10px;
		color: #999999;
		font-size: 12px;
		font-weight: 700;
		line-height: 17px;
		text-align: center; }
	&__tel {
		font-weight: 700;
		line-height: 17px;
		color: #000000;
		font-size: 15px;
		text-align: right; } }

.experts {
	// text-align: justify
	margin-right: -24px;
	font-size: 0; }
