.select__wrapp {
	width: 100%;
	border-radius: 5px; }

.select__bar {
	position: relative;
	cursor: pointer;
	margin-top: 7px;
	width: 100%;
	border-radius: 5px;
	background-color: #ffffff;
	padding: 0 15px;
	line-height: 44px;
	color: #b7c3c7;
	font-size: 1.125em;
	font-weight: 400;
	border-bottom: 1px solid #fff;
	&:after {
		content: "";
		display: block;
		position: absolute;
		top: 20px;
		right: 14px;
		@include s(arrow_down); }
	&.selected {
		color: #000; }
	&.is-active {
		border-radius: 5px 5px 0 0;
		border-bottom: 1px solid #cbd3d6;
		&:after {
			@include s(arrow_up); } } }

.select {
	display: none; }

/* -------------------------------- */
.select__dropdown {
	position: absolute;
	z-index: 10;
	display: none;
	width: 100%;
	border-radius: 5px;
	background-color: #ffffff;
	color: #b7c3c7;
	font-size: 0.875em;
	font-weight: 400;
	overflow: hidden;
	padding: 15px 0;
	&.is-active {
		color: #000;
		border-radius: 0 0 5px 5px; }
	.option {
		position: relative;
		border-left: 3px solid transparent;
		display: block;
		padding: 0 15px;
		line-height: 2.1em;
		&:hover {
			cursor: pointer;
			background-color: #eee; }
		&_active {
			left: -5px;
			&:before {
				content: "";
				position: relative;
				top: -2px;
				left: -5px;
				display: inline-block;
				width: 6px;
				height: 9px;
				border-right: 2px solid #e1762e;
				border-bottom: 2px solid #e1762e;
				transform: rotate(45deg); } } }
	&:last-child {
		border-radius: 0 0 5px 5px; } }

